.call_to_buy {
  height: auto;
}

.pic_call_to_buy img {
  max-width: 980px;
  max-height: 608px;
  min-width: none;
  margin: 30px auto 0 auto;
  overflow: hidden;
}
.pic_call_to_buy {
  position: relative;
  top: 100px;
}

.main_call_to_buy {
  max-width: 1240px;
  height: 628px;
  padding: 74px;
  background: #fff;
  border-radius: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-image: url(./elementBg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1086px 480px;
  gap: 20px;
}

.main_call_to_buy h2 {
  max-width: 756px;
  color: #424242;
  text-align: center;
  font-family: BlissPro;
  font-size: 45px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
}
.main_call_to_buy h4 {
  color: #0f1f1c;
  text-align: center;
  font-family: BlissPro;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration-line: line-through;
  text-transform: uppercase;
  opacity: 0.5;
}
.main_call_to_buy h1 {
  color: #f55656;
  text-align: center;
  font-family: BlissPro;
  font-size: 96px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
}
.main_call_to_buy p {
  color: #f55656;
  font-family: BlissPro;
  font-size: 32px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
}
.main_call_to_buy button {
  width: 247px;
  height: 79px;
  border-radius: 27px;
  background: #f58d56;
  color: #fefefe;
  font-family: BlissPro;
  font-size: 20px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  border: none;
}
/* ---------------------Adaptive------------------------- */
@media (width < 1000px) {
  .pic_call_to_buy img {
    width: 800px;
    height: 500px;
  }
  .main_call_to_buy {
    background-size: 886px 280px;
  }
}
@media (width < 800px) {
  .pic_call_to_buy img {
    width: 700px;
    height: 400px;
  }
}
@media (width < 700px) {
  .pic_call_to_buy img {
    width: 500px;
    height: 300px;
  }
}
@media (width <= 430px) {
  .pic_call_to_buy img {
    width: 310px;
    height: 200px;
  }
  .main_call_to_buy h2 {
    font-size: 38px;
  }
}
