.header_about_bracelet {
  text-align: left;
  padding: 57px 0 57px 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 356px;
  width: auto;
}
.about_bracelet {
  display: flex;
}

.header h2 {
  color: #fff;
  font-family: CenturyGothic;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
}
.header h1 {
  color: #fff;
  font-family: CenturyGothic;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
}
.header_description {
  width: 456px;
  height: 90px;
}
.header_description p {
  color: #d9d9d9;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  opacity: 0.5;
}
.header_about_bracelet button {
  width: 247px;
  height: 79px;
  border-radius: 27px;
  background: #f58d56;
  color: #fefefe;
  font-family: BlissPro;
  font-size: 20px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  border: none;
}

/* _------______-------____----__--ADAPTIVE--__----___-------_____------ */
@media (width < 1440px) {
  .about_bracelet {
    flex-direction: column;
  }
  .header_about_bracelet {
    margin: auto;
    text-align: center;
    padding: 57px 0;
  }
}
@media (width <= 1024px) {
  /* .about_bracelet {
    flex-direction: column;
  } */
}
@media (width <= 430px) {
  .header_about_bracelet {
    width: 320px;
  }
  .header_description {
    width: 300px;
    height: auto;
    margin: 30px auto;
  }
}
