.block {
  width: auto;
  height: auto;
  border-radius: 30px;
  background: linear-gradient(#353535, #131313);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  justify-content: center;
}
.block h2 {
  color: #fff;
  font-family: BlissPro;
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.block p {
  color: #f58d56;
  font-family: BlissPro;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.demo_list_block {
  max-width: 1240px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: inherit;
}
