.slider {
  padding: 57px 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  left: 2px;
}
.slide,
.slide img {
  border-radius: 45px;
  width: 100%;
  height: auto;
  margin-right: 20px;
  outline: none;
  overflow: hidden;
}
.slide {
  opacity: 0.5;
  transform: scale(0.7);
  transition: 0.5;
}
.slide_active {
  opacity: 1;
  transform: scale(1);
}
@media (width <= 480px) {
  .slider {
    margin: 80px auto 0 auto;
    height: 206px;
    padding: 0;
  }
}
