.main_header_bg {
  height: 951px;
  padding: 57px 100px;
  background: linear-gradient(132deg, #f58d56 -17.38%, #112120 78.4%);
  border-radius: 60px 60px 0 0;
  /* margin: auto; */
  box-sizing: border-box;
}
.main_header_bg::before {
  content: ' ';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 852px;
  border-bottom: 100px solid #141414;
  border-right: 50vw solid transparent;
  z-index: 3;
}
.main_header_bg::after {
  content: ' ';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  right: 0;
  top: 852px;
  border-bottom: 100px solid #141414;
  border-left: 50vw solid transparent;
  z-index: 3;
}

.menu {
  display: flex;
  justify-content: space-around;
}
.menu_list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 30px;
}
.title {
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
}

.title_stock p {
  text-align: center;
  padding: 14px 34px;
  background: #f55656;
  color: #fff;
  font-family: BlissPro;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  border-radius: 15px;
  max-width: 310px;
  margin-bottom: 43px;
}

.title_Heading h2 {
  color: #fff;
  font-family: BlissPro;
  font-size: 48px;
  font-style: normal;

  line-height: normal;
  text-transform: uppercase;
}
.title_Heading h1 {
  color: #fff;
  font-family: BlissPro;
  font-size: 96px;
  font-style: normal;
  font-weight: 900;
  line-height: 0%;
  text-transform: uppercase;
  margin-top: 40px;
  margin-bottom: 40px;
}
.title_desc {
  max-width: 519px;
  color: #fff;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  opacity: 0.5;
  margin-top: 43px;
  margin-bottom: 35px;
}
.price_button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  margin-top: 100px;
}
.price_button button {
  width: 292px;
  height: 91px;
  border-radius: 27px;
  background: #f58d56;
  border: none;
  color: #fefefe;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
}
.price_box {
  text-align: center;

  margin-left: 60px;
}
.price {
  background: #f55656;
  border-radius: 15px;
  width: 338px;
  height: 114px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform: skewX(-15deg);
  z-index: 0;
}
.price h1 {
  color: #fff;
  text-align: center;
  font-family: BlissPro;
  font-size: 86px;
  font-style: normal;
  font-weight: 800;
  line-height: 0.7;
  text-transform: none;
  transform: skewX(15deg);
}
.price h2 {
  color: #fff;
  font-family: BlissPro;
  font-size: 20px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  transform: skewX(15deg);
}
.price_old {
  background-color: #ffffff;
  border-radius: 15px;
  width: 187px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform: skewX(-15deg);
  position: relative;
  left: 128px;
  top: 12px;
  z-index: 1;
}
.price_old h1 {
  color: #424242;
  text-align: center;
  font-family: BlissPro;
  font-size: 36px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-decoration-line: line-through;
  text-transform: uppercase;
  transform: skewX(15deg);
}
.price_old h2 {
  color: #424242;
  font-family: BlissPro;
  font-size: 15px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-decoration-line: line-through;
  text-transform: uppercase;
  transform: skewX(15deg);
}
.content_header {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  position: relative;
  top: 0;
  left: 0;
}

.image_title {
  position: absolute;
  top: 20px;
  right: -108px;
  width: 904px;
  height: 904px;
  background: url(./ContentHeaderComponent/xiaomi_band_7.png) no-repeat;
  z-index: 2;
}
