/* ---------------------Adaptive------------------------- */
@media (width < 1440px) {
  .title {
    z-index: 1;
  }
  .image_title {
    z-index: 0;
    opacity: 0.6;
  }
}
@media (width <= 1024px) {
  .title_Heading h1 {
    /* text-shadow: 45px 0 50px #141414; */
  }
  .title_desc {
    opacity: 0.9;
  }
}
@media (width <= 768px) {
  .main_header_bg {
    padding: 57px 50px;
  }
  .image_title {
    right: -50px;
  }
}
@media (width <= 600px) {
  .main_header_bg {
    padding: 57px 0;
  }
  .title_desc {
    margin: 43px auto;
    max-width: 250px;
    text-align: center;
  }
  .title_Heading h1 {
    font-size: 76px;
  }
  .title_Heading h2 {
    font-size: 36px;
  }
  .title {
    text-align: center;
  }
  .title_stock {
    margin: auto;
    width: 250px;
  }
  .price_button {
    margin-top: 50px;
  }
  .image_title {
    width: 500px;
    right: 0;

    overflow: hidden;
  }
}
@media (width <= 480px) {
  .main_header_bg {
    padding: 57px 10px;
  }
}
@media (width <= 440px) {
  .main_header_bg {
    padding: 57px 10px;
  }
  .main_header_bg {
    padding: 57px 0;
  }
  .title_desc {
    margin: 43px auto;
    max-width: 250px;
    text-align: center;
    text-shadow: 10px 10px 10px black;
  }
  .price {
    width: 290px;
  }
  .title_Heading {
    width: 320px;
    margin: auto;
  }
  .title_Heading h1 {
    font-size: 52px;
  }
  .title_Heading h2 {
    font-size: 26px;
  }
  .title {
    text-align: center;
    width: 320px;
    /* margin: auto; */
    position: relative;
  }
  .title_stock {
    width: 200px;
  }
  .price_button {
    margin-top: 50px;
  }
  .image_title {
    width: 500px;
    right: 0;

    overflow: hidden;
  }
  .price_box {
    margin: auto;
  }
  .main_header_bg::before {
    top: 852px;
  }
  .main_header_bg::after {
    top: 852px;
  }
  .price_old {
    left: 106px;
  }
}
