.slider_2 {
  padding: 57px 0;
  width: 610px;
  height: 250px;
  overflow: hidden;
  position: relative;
  left: 2px;
  background-image: url(../Bg_100dials.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 60px;
}
.slide_2,
.slide_2 img {
  /* border-radius: 45px; */
  max-width: 103px;
  max-height: 249px;
  padding: 5px;
  outline: none;
  overflow: hidden;
  position: relative;
  left: -7px;
  top: 0px;
}
.slide_2 {
  opacity: 0.5;
  /* transform: scale(0.7); */
  transition: 0.5;
}
.slide_active_2 {
  opacity: 1;
  /* transform: scale(1); */
}
@media (width <= 630px) {
  .slider_2 {
    width: 510px;
    height: 200px;
  }
  .slide_2,
  .slide_2 img {
    /* border-radius: 45px; */
    width: 92px;
    height: 213px;

    left: -6px;
    top: -5px;
  }
}
@media (width <= 430px) {
  .slider_2 {
    width: 390px;
    height: 160px;
  }
  .slide_2,
  .slide_2 img {
    width: 76px;
    padding: 10px;
    margin: 10px;
    height: 186px;
    top: -20px;
    left: -23px;
  }
  .slick-slider {
    top: -7px;
  }
}
@media (width < 390px) {
  .slider_2 {
    width: 340px;
    height: 130px;
  }
  .slide_2,
  .slide_2 img {
    width: 65px;
    padding: 10px;
    margin: 10px;
    height: 163px;
    top: -20px;
    left: -23px;
  }
  .slick-slider {
    top: -11px;
  }
}
