.block_detail {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 57px 100px;
  max-width: 1240px;
  height: auto;
  margin: auto;
  gap: 45px;
}
.pic_detail img {
  width: 610px;
  height: 302px;
  border-radius: 60px;
}

.desc_detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 565px;
  text-align: left;
  gap: 25px;
  height: auto;
}
.desc_detail h1 {
  color: #fff;
  font-family: BlissPro;
  font-size: 45px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.desc_detail p {
  color: #d9d9d9;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  opacity: 0.5;
}
.desc_detail_right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: auto;
  max-width: 565px;
  text-align: right;
  gap: 25px;
}
.desc_detail_right h1 {
  color: #fff;
  font-family: BlissPro;
  font-size: 45px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.desc_detail_right p {
  color: #d9d9d9;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  opacity: 0.5;
}
/* ____________________________---ADAPTIVE---_________________________ */
@media (width <= 480px) {
  .pic_detail img {
    width: 340px;
    height: 170px;
  }
  .block_detail {
    padding: 57px 50px;
  }
  /* .desc_detail_right {
    width: 310px;
  } */
  .desc_detail_right h1 {
    font-size: 35px;
  }
}
@media (width <= 370px) {
  .pic_detail img {
    width: 320px;
    height: 170px;
  }
}
