.footer {
  margin: 74px auto 10px auto;
  display: flex;
  flex-direction: column;
  gap: 100px;
}

.footer_heading h2 {
  color: #fff;
  font-family: BlissPro;
  font-size: 48px;
  font-style: normal;

  line-height: normal;
  text-transform: uppercase;
}
.footer_heading h1 {
  color: #fff;
  font-family: BlissPro;
  font-size: 96px;
  font-style: normal;
  font-weight: 900;
  line-height: 0%;
  text-transform: uppercase;
  margin-top: 40px;
  margin-bottom: 40px;
}
.footer button {
  width: 292px;
  height: 91px;
  border-radius: 27px;
  background: #f58d56;
  border: none;
  color: #fefefe;
  font-family: BlissPro;
  font-size: 25px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
}
.footer_menu {
  margin: 50px;
}
@media (width <= 430px) {
  .footer_heading h1 {
    font-size: 52px;
  }
  .footer_heading h2 {
    font-size: 30px;
  }
}
