.demo_block {
  max-width: 1240px;
  height: auto;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.demo_head {
  max-width: 565px;
  height: 54px;
  text-align: center;
  padding: 57px;
  margin: auto;
}
.demo_head h1 {
  color: #fff;
  text-align: center;
  font-family: Bliss Pro;
  font-size: 45px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.demo_wrapper {
  max-width: 1240px;
  height: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.demo_pic img {
  border-radius: 60px;
  padding-bottom: 15px;
}
/* ____________________________---ADAPTIVE---_________________________ */
@media (width <= 480px) {
  .demo_pic img {
    width: 310px;
    height: 150px;
  }
}
